@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
}

html, body {
  font-family: 'Roboto', sans-serif;
  background-color: #CECECE;
}
.app {
  width: 100vw;
  height: 100vh;
}
.canvas {
  height: calc(100vh - 40px);
  position: relative;
  padding: 0 30px;
}

.canvas {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: flex-start;
}

.canvas .note {
  margin: 10px;
  position: relative;
}
.note {
  width: 300px;
}

.note .header {
  padding: 0 10px;
  height: 40px;
  background-color: #e0d72a;
  line-height: 40px;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
}

.note .header input {
  background-color: #00000000;
  border: none;
  outline: none;
  width: 100%;
}

.note .content {
  height: 240px;
  padding: 10px;
  background-color: #FFF740;
}

.note .content textarea {
  width: 100%;
  height: 100%;
  background-color: #00000000;
  border: none;
  outline: none;
  resize: none;
}
.menubar {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 40px;
  line-height: 40px;
  font-weight: bold;
  background-color: #FF65A3;
  display: flex;
  justify-content: space-between;
}

.menubar p {
  padding: 0 40px;
}

.menubar button {
  background-color: #7AFCFF;
  border: none;
  padding: 0 40px;
  text-align: center;
  text-decoration: none;
  font-weight: bold;
}

.menubar button:hover {
  background-color: #4fccce;
}

@media screen and (max-width: 600px) {
  .menubar p {
    padding: 0 10px;
    font-size: 12px;
  }
  .menubar button {
    padding: 0 10px;
  }
}
